import { StrictMode } from "react";
import { createRoot } from "react-dom/client";
import { createBrowserRouter, createHashRouter, RouterProvider } from "react-router-dom";

import App from "./components/App"; // Import App component
import HomePage from "./pages/home";
import Quiz from "./pages/quiz/components/Quiz";
import FillInQuiz from "./pages/quiz/components/FillInQuiz";
import { AppProvider } from "./store/contexts/AppContext"; // Import AppProvider
import SystemSettingsPage from "./pages/systemSettings";
import NotebookManagementPage from "./pages/notebookManagement";
import NavigationBlocker from "./components/NavigationBlocker";
import Reading from "./pages/Reading/components/Reading";
import WordTest from "./pages/wordTest"; 
import DiagnosticsPage from "./pages/diagnostics"; // 新增：診斷頁

const rootElement = document.getElementById("root");
const root = createRoot(rootElement);

// 動態決定 basename：
// - Capacitor (capacitor:// 或 http://localhost 的情況) 使用 "/"
// - 若 PUBLIC_URL 存在且不是 "." 則用其 pathname
// - 若部署在 *.github.io 的 project page，從目前路徑推導第一段（/repoName）
// - 其他情況預設 "/"
const isCapacitorLike = () => {
  const href = window.location.href;
  return (
    href.startsWith("capacitor://") ||
    href.startsWith("http://localhost") ||
    href.startsWith("https://localhost")
  );
};

function computeBasename() {
  if (isCapacitorLike()) return "/";

  const pub = process.env.PUBLIC_URL;
  if (pub && pub !== ".") {
    try {
      const p = new URL(pub, window.location.origin).pathname.replace(/\/$/, "");
      return p || "/";
    } catch {
      return pub.startsWith("/") ? pub.replace(/\/$/, "") : "/";
    }
  }

  const { hostname, pathname } = window.location;
  if (hostname.endsWith("github.io")) {
    const seg = pathname.split("/").filter(Boolean);
    if (seg.length > 0) return `/${seg[0]}`; // e.g. /JP_word
  }
  return "/";
}

const routes = [
  {
    path: "/",
    element: <App />,
    children: [
      {
        index: true,
        element: <HomePage />,
      },
      {
        path: "quiz",
        element: (
          <NavigationBlocker
            clearOnConfirm
            considerQuiz={false}
            considerReading={true}
            considerFillin={true}
            message="偵測到進行中的學習任務，將會刪除進度，是否繼續？"
          >
            <Quiz />
          </NavigationBlocker>
        ),
      },
      {
        path: "fillin",
        element: (
          <NavigationBlocker
            clearOnConfirm
            considerQuiz={true}
            considerReading={true}
            considerFillin={false}
            message="偵測到進行中的學習任務，將會刪除進度，是否繼續？"
          >
            <FillInQuiz />
          </NavigationBlocker>
        ),
      },
      {
        path: "word_test",
        element: (
          <NavigationBlocker
            clearOnConfirm
            message="偵測到進行中的學習任務，將會刪除進度，是否繼續？"
          >
            <WordTest />
          </NavigationBlocker>
        ),
      },
      {
        path: "notebook-management",
        element: (
          <NavigationBlocker
            clearOnConfirm
            message="偵測到進行中的學習任務，將會刪除進度，是否繼續？"
          >
            <NotebookManagementPage />
          </NavigationBlocker>
        ),
      },
      {
        path: "settings",
        element: (
          <NavigationBlocker
            clearOnConfirm
            message="偵測到進行中的學習任務，將會刪除進度，是否繼續？"
          >
            <SystemSettingsPage />
          </NavigationBlocker>
        ),
      },
      {
        path: "reading",
        element: (
          <NavigationBlocker
            clearOnConfirm
            considerQuiz={true}
            considerReading={false}
            considerFillin={true}
            message="偵測到進行中的學習任務，將會刪除進度，是否繼續？"
          >
            <Reading />
          </NavigationBlocker>
        ),
      },
      {
        path: "diagnostics",
        element: <DiagnosticsPage />,
      },
    ],
  },
];

const router = isCapacitorLike()
  ? createHashRouter(routes)
  : createBrowserRouter(routes, { basename: computeBasename() });

root.render(
  <StrictMode>
    <AppProvider>
      <RouterProvider router={router} />
    </AppProvider>
  </StrictMode>
);
